.SectionHomeSlider {
    padding: 60px 0;
    border-bottom: 1px solid #f3f3f3;
}

.SectionHomeSlider-container {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}

.tp-container {
    width: 100%;
    position: relative;
    padding: 0;
}
.tp-slideshow {
    opacity: 1;
    visibility: visible;
    height: 550px;
}

.tp-slideshow-mainul{
    display: flex;
    justify-content: center;
    overflow: hidden;
    width: 100%;
    height: 100%;
}

.tp-slideshow-active {
    width: 1200px;
    height: 100%;
    justify-content: center;
    align-items: center;
}
.tp-slideshow-active li{
    position: absolute;
}
.slotholder{
    width: 100%;
    height: 100%;
}

.tp-bgimg {
    background: #f2f2f2;
    background-image: url("/src/assets/img/background-1747789_1280.jpg");
    background-repeat: no-repeat;
    background-position: center center;
    width: 100%;
    height: 100%;
}

.caption {
    z-index: 6;
    min-height: 0px;
    min-width: 0px;
    line-height: 24px;
    border-width: 0px;
    margin: 0px;
    padding: 0px;
    letter-spacing: 0px;
    font-size: 14px;
    left: 200px;
    top: 135.5px;
}

.caption-text-01 {
    position: absolute;
    top: 100px;
    left: 400px;
    line-height: 1.2;
    color: #3e3e3e;
    font-size: 12px;
}
