    background: #8f8f8f;
}
.MenuToggle span::before {
    content: "";
    position: absolute;
    top:8px;
    left: 0;
    width: 100%;
    height: 2px;
    background: #8f8f8f;
}

.MenuToggle span::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 8px;
    width: 100%;
    height: 2px;
    background: #8f8f8f;
}

