}



.more__btn::after {
    position: absolute;
    left: 10px; 
    top: 11px; 
    content: '';
    width: 7px; /* 사이즈 */
    height: 7px; /* 사이즈 */
    border-top: 1px solid #959595; /* 선 두께 */
    border-right: 1px solid #959595; /* 선 두께 */
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg); /* 각도 */
}

.more__btn:hover::after {
    position: absolute;
    left: 10px; 
    top: 11px; 
    content: '';
    width: 7px; /* 사이즈 */
    height: 7px; /* 사이즈 */
    border-top: 1px solid #fff; /* 선 두께 */
    border-right: 1px solid #fff; /* 선 두께 */
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg); /* 각도 */
}


.more__btn:hover{
    background: #a9803c;
}

* {margin:0;padding:0;box-sizing:border-box}
/* reset */
html{font-size: 13px}
body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,legend,textarea,p,blockquote,th,td,input,select,textarea,button {margin:0;padding:0}
fieldset,img {border:0 none}
dl,ul,ol,menu,li {list-style:none}
blockquote, q {quotes: none}
blockquote:before, blockquote:after,q:before, q:after {content:'';content:none}
input,select,textarea,button {vertical-align:middle}
button {border:0 none;background-color:transparent;cursor:pointer}
body {background:#fff}
body,th,td,input,select,textarea,button {font-family:'notokr-regular',sans-serif;font-weight:400;}
/* color값은 디자인가이드에 맞게사용 */
a {color:#333;text-decoration:none}
i {font-style:normal}
a:active, a:hover {text-decoration:none}
address,caption,cite,code,dfn,em,var {font-style:normal;font-weight:normal}
h1,h2,h3,h4,h5 {font-family: 'notokr-regular',sans-serif;font-weight:400;}

